<script>
    import { createEventDispatcher, onMount } from 'svelte';
    import { scale } from 'svelte/transition';
    import { sineInOut } from 'svelte/easing';

    const dispatch = createEventDispatcher();

    let road, police;

    onMount(onLoad);

    function onLoad() {
        const items = document.querySelectorAll('.menu-item');

        for(var i = 0, l = items.length; i < l; i++) {
        items[i].style.left = (50 - 35*Math.cos(-0.5 * Math.PI - 2*(1/l)*i*Math.PI)).toFixed(4) + "%";
        
        items[i].style.top = (50 + 35*Math.sin(-0.5 * Math.PI - 2*(1/l)*i*Math.PI)).toFixed(4) + "%";
        }
    }

</script>

<div class="container" transition:scale={{duration: 500, easing: sineInOut}}>

    <div class="close">
        <button on:click={()=> dispatch('closeMenu')}>
        
            <img src="./icons/button-cancel.png" alt="close">
        
        </button>
    </div>
    
<!-- POLICE -->
    <div class="menu-item">
        <button on:click={()=> police = true}>
            
            <img src="./icons/police-car.png" alt="police">
            
        </button>
        {#if police}
            <div class="fixed -mt-32">
                <button on:click={()=> dispatch('police')} on:click={()=> dispatch('closeMenu')}>
            
                    <img src="./icons/police-car.png" alt="police">
                    
                </button>
            </div>
            <div class="fixed -ml-16 -mt-32">
                <button on:click={()=> dispatch('mCamera')} on:click={()=> dispatch('closeMenu')}>

                    <img src="./icons/speed-camera.png" alt="camera">

                </button>
            </div>
            <div class="fixed ml-16 -mt-32">
                <button on:click={()=> dispatch('sCamera')} on:click={()=> dispatch('closeMenu')}>
                    
                    <img src="./icons/road-camera.png" alt="rcamera">
                    
                </button>
            </div>
        {/if}
    </div>


<!-- ROAD -->

    <div class="menu-item">
        <button on:click={()=> road = true}>
            
            <img src="./icons/road-1.png" alt="road">
            
        </button>
        {#if road}
            <div class="fixed ml-16">
                <button on:click={()=> dispatch('rConstruction')} on:click={()=> dispatch('closeMenu')}>
            
                    <img src="./icons/road-construction.png" alt="rc">
                    
                </button>
            </div>
            <div class="fixed ml-16 -mt-16">
                <button on:click={()=> dispatch('rClosed')} on:click={()=> dispatch('closeMenu')}>
            
                    <img src="./icons/road-closed.png" alt="closed">
            
                </button>
            </div>
            <div class="fixed ml-16 -mt-32">
                <button on:click={()=> dispatch('dtp')} on:click={()=> dispatch('closeMenu')}>
            
                    <img src="./icons/car-crash.png" alt="dtp">
            
                </button>
            </div>
        {/if}
    </div>

<!-- ADD -->

    <div class="menu-item">
        <button on:click={()=> dispatch('add')} on:click={()=> dispatch('closeMenu')}>
            
            <img src="./icons/chat-3.png" alt="add">
            
        </button>
    </div>

<!-- TAXI -->

    <div class="menu-item">
        <button on:click={()=> dispatch('taxiCall')} on:click={()=> dispatch('closeMenu')}>
            
            <img src="./icons/taxi-24.png" alt="taxiCall">
            
        </button>
    </div>

<!-- ADDRESS -->

    <div class="menu-item">
        <button on:click={()=> dispatch('getAddress')} on:click={()=> dispatch('closeMenu')}>
            
            <img src="./icons/location-info.png" alt="address">
            
        </button>
    </div>

<!-- SOS -->

    <div class="menu-item">
        <button on:click={()=> dispatch('sos')} on:click={()=> dispatch('closeMenu')}>
            
            <img src="./icons/sos-button.png" alt="sos">
            
        </button>
    </div>

</div>

<style>.container {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  margin-top: -50%;
  margin-left: -50%;
}

.menu-item {
  position: absolute;
  margin-left: -30px;
  margin-top: -30px;
}

button {
  padding: 0.5rem;
  width: 4rem;
  height: 4rem;
  border-width: 1px;
  --border-opacity: 1;
  border-color: #4a5568;
  border-color: rgba(74, 85, 104, var(--border-opacity));
  border-radius: 9999px;
  --bg-opacity: 1;
  background-color: #fff;
  background-color: rgba(255, 255, 255, var(--bg-opacity));
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  outline: 2px solid transparent;
  outline-offset: 2px;
}

img {
  -o-object-fit: cover;
     object-fit: cover;
}</style>