<script>
    import {firebase, rdb} from './../firebase.js';
    import { onMount, onDestroy, createEventDispatcher } from 'svelte';
    import axios from 'axios';
    export let map, mapboxgl, user, userLocation;
    const dispatch = createEventDispatcher()
    let pinMarker
    let userAddress = {}
    let points = []
    let inputTxt
    let pointMarker
    let searchResults = []
    let newPointBlock
    let setPointFromMap
    let orderOptions = {}

    onMount(addPinMarkerToMap)
    onMount(getUserAddress)

    onDestroy(() => {
        if (pinMarker) pinMarker.remove()
        if (pointMarker) pointMarker.remove()
	});

    function addPinMarkerToMap() {
        let lngLat = userLocation.lngLat

        if (!lngLat) lngLat = map.getCenter()

        const el = document.createElement('div');
            el.className = 'marker';
            el.style.background = "no-repeat center/contain url('/icons/pin.svg')";
        pinMarker = new mapboxgl.Marker({
            element: el,
            draggable: true
        })
        .setLngLat(lngLat)
        .addTo(map);

        pinMarker.on('dragend', pinMarkerDragEnd)

        map.jumpTo({
            center: lngLat,
            zoom: 16
        });
        
    }

    async function pinMarkerDragEnd(e) {
        const lngLat = e.target.getLngLat()
        const response = await fetch(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${lngLat.lat}&lon=${lngLat.lng}&email=${user.email}`);
        const data = await response.json();
        // console.log('data', data)
        userAddress = data;
    }

    async function getUserAddress() {
        let lngLat = userLocation.lngLat
        if (!lngLat) lngLat = map.getCenter()
        const response = await fetch(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${lngLat.lat}&lon=${lngLat.lng}&email=${user.email}`);
        const data = await response.json();
        // console.log('data', data)
        userAddress = data;
        getUserOrders()
    }

    async function getPointMarkerAddress() {
        const lngLat = pointMarker.getLngLat()
        const response = await fetch(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${lngLat.lat}&lon=${lngLat.lng}&email=${user.email}`);
        const data = await response.json();
        // console.log('data', data)
        points.push(data)
        points = points
        newPointBlock = false
    }
    
    function inputHandler() {
        const space = inputTxt.includes(' ')
        if (space) searchAddress()
    }

    function searchAddress() {
        const requestConfig = { 
            params: {
                street: inputTxt,
                city: userAddress.address.city,
                limit: 5,
                countrycodes: 'ru',
                addressdetails: 1,
                extratags: 1,
                polygon_geojson: 1,
                format: 'jsonv2'
            },
            withCredentials: false,
            headers: {'Access-Control-Allow-Origin': null}
        };

        axios.get('https://nominatim.openstreetmap.org/search/', requestConfig)
        .then(function (response) {

            searchResults = response.data;

        })
        .catch(function (error) {
            console.log(error);
        })
    }

    function addPointMarkerToMap(addr) {

        points.push(addr)
        points = points
        console.log(points)

        const el = document.createElement('div');
            el.className = 'marker';
            el.style.background = "no-repeat center/contain url('/icons/pin.svg')";
        let pointMarker = new mapboxgl.Marker({
            element: el,
            draggable: true
        })
        .setLngLat([+addr.lon, +addr.lat])
        .addTo(map);

        searchResults = []
        inputTxt = ''

        map.panTo([+addr.lon, +addr.lat])
    }

    function addPointMarkerFromMap() {

        if (!setPointFromMap) {

            if (pointMarker) return

            const el = document.createElement('div');
            el.className = 'marker';
            el.style.background = "no-repeat center/contain url('/icons/pin.svg')";

            pointMarker = new mapboxgl.Marker({
                element: el,
                draggable: true
            })
            .setLngLat(map.getCenter())
            .addTo(map);

            map.on('drag', replacePointMarker)
            setPointFromMap = true
        } else {
            // get new address & push to points
            map.off('drag', replacePointMarker)
            setPointFromMap = false

            getPointMarkerAddress()
        }

    }

    function replacePointMarker() {
        if (setPointFromMap) pointMarker.setLngLat(map.getCenter())
    }

    function deletePoint(i) {
        points.splice(i, 1)
        points = points
    }

    function createOrder() {
        const a = userAddress.address;
        if (!a.state) return alert('ะัะธะฑะบะฐ ะณะตะพะปะพะบะฐัะธะธ, ะฒะบะปััะธัะต GPS ะธ ะฟะพะฒัะพัะธัะต ะฟะพะฟััะบั.')
        const locality = a.city ? a.city : a.town ? a.town : a.village ? a.village : a.state
        console.log('locality', locality)

        const taxiOrders = rdb.ref(`taxiOrders/${a.state}/${locality}`)
        const newOrder = taxiOrders.push()
        newOrder.set({
            owner: user.uid,
            options: orderOptions,
            points: points,
            status: 'new'
        })
        .then((response)=> {

            console.log('newOrder', response)
        })
        .catch((error)=> {

            console.log('error', error)
        })
    }

    function getUserOrders() {
        const a = userAddress.address;
        if (!a.state) return alert('ะัะธะฑะบะฐ ะณะตะพะปะพะบะฐัะธะธ, ะฒะบะปััะธัะต GPS ะธ ะฟะพะฒัะพัะธัะต ะฟะพะฟััะบั.')
        const locality = a.city ? a.city : a.town ? a.town : a.village ? a.village : a.state
        console.log('locality', locality)

        const taxiOrders = rdb.ref(`taxiOrders/${a.state}/${locality}`)
        taxiOrders.on('value', (snapshot) => {
            const data = snapshot.val();
            console.log('data', data)
            // const userOrders = data.filter(el=> el.owner === user.uid)
            // console.log('userOrders', userOrders)
        });
    }
    
</script>

<div class="p-2">
    
    <!-- From -->
    <div>
        <span class="txt-1">
            ะัะบัะดะฐ: 
        </span>
        <span class="txt-2">
            {#if userAddress.display_name}
                {userAddress.display_name.substr(0, 40)}
            {:else}
                ะฃััะฐะฝะพะฒะธัะต ะผะฐัะบะตั ะฝะฐ ะบะฐััะต
            {/if}
        </span>
    </div>

    <!-- Points -->
    {#if points.length > 0}
        <div class="mt-1">
            {#each points as p, i}
                <div>
                    <span class="txt-1">
                        ะัะดะฐ: 
                    </span>
                    <span class="txt-2">
                        {p.display_name.substr(0, 70)}...
                    </span>
                    <button class="btn-gray" on:click="{()=> deletePoint(i)}">
                        ัะดะฐะปะธัั
                    </button>
                </div>
            {/each}
        </div>
    {/if}

    <!-- New point -->
    {#if points.length < 1 || newPointBlock}
        <div class="mt-2">
            <span class="txt-1">
                ะัะดะฐ: 
            </span>
            <input type="text" placeholder="ะฃะปะธัะฐ ะดะพะผ" bind:value="{inputTxt}" on:input="{inputHandler}">
            {#if !setPointFromMap}
                <button class="btn-green" on:click="{addPointMarkerFromMap}">
                    ะัะฑัะฐัั ะฝะฐ ะบะฐััะต
                </button>
            {:else}
                <button class="btn-green" on:click="{addPointMarkerFromMap}">
                    ะะพัะพะฒะพ
                </button>
            {/if}
        </div>
    {:else}
        <div class="text-center">
            <button class="btn-green" on:click="{()=> newPointBlock = true}">
                ะะพะฑะฐะฒะธัั ัะพัะบั
            </button>
        </div>
    {/if}

    <!-- Search results -->
    {#if searchResults.length > 0}
        <div class=" bg-white rounded-md overscroll-y-auto">
            {#each searchResults as addr}
                <div on:click="{()=> addPointMarkerToMap(addr)}" class="bg-gray-200 m-1 p-1">
                    {addr.display_name.substr(0, 40)}...
                </div>
            {/each}
        </div>
    {/if}

    <!-- Order options -->
    <div class="mt-2 text-center">
        <label>
            ะะตััะบะพะต ะบัะตัะปะพ
            <input type="checkbox" bind:checked="{orderOptions.childSeat}">
        </label>
        <label class="ml-2">
            ะะฐะณะฐะถ
            <input type="checkbox" bind:checked="{orderOptions.baggage}">
        </label>
        <label class="ml-2">
            ะะพะผ. ะถะธะฒะพัะฝะพะต
            <input type="checkbox" bind:checked="{orderOptions.animal}">
        </label>
    </div>

    <!-- Order amount -->
    <div class="mt-2 text-center">
        <label for="">
            ะกัะพะธะผะพััั ะฟะพะตะทะดะบะธ ััะฑ.: 
            <input type="number" bind:value="{orderOptions.amount}" class="ml-2 w-24">
        </label>
    </div>
    
    <!-- Buttons -->
    <div class="mt-4 flex flex-row items-center justify-around">
        <div>
            <button class="btn-gray" on:click="{()=> dispatch('close')}">
                ะัะผะตะฝะฐ
            </button>
        </div>
        <div>
            <button class="btn-orange" on:click="{createOrder}">
                ะัะพัะผะธัั
            </button>
        </div>
    </div>
</div>

<style>.txt-1 {
  font-size: 0.875rem;
  font-weight: 600;
}

.txt-2 {
  margin-left: 0.5rem;
  font-size: 0.875rem;
  --text-opacity: 1;
  color: #718096;
  color: rgba(113, 128, 150, var(--text-opacity));
}

label {
  font-size: 0.875rem;
}</style>