<script>
    import {createEventDispatcher} from 'svelte';
    import { fade } from 'svelte/transition';
    export let message = ''
    export let timeout = 5000

    const dispatch = createEventDispatcher();

    setTimeout(()=> {
        dispatch('close')
    }, timeout)

</script>

<div id="alertBox" class=" fixed top-0">

    <div transition:fade="{{ duration: 500 }}" class=" h-auto w-auto p-3 border bg-white rounded-lg shadow-lg">

        <p class="mt-2 text-center">
            {message}
        </p>

        <p class="mt-4 text-center">
            <button class="btn-orange py-2 px-6" on:click={() => dispatch('close')}>ะะบ</button>
        </p>

    </div>

</div>

<style>#alertBox {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}</style>